.explore-online {
  &__wrapper {
    display: grid;
    row-gap: 48px;
    @include pageGrid;
  }
}

.online-exhibition {
  display: grid;
  row-gap: 24px;
  align-self: start;

  grid-column: 1 / -1;
  @include onTablet {
    grid-column: 2 / -2;
  }
  @include onDesktop {
    grid-column: span 4;
  }

  &__link {
    position: relative;
    padding-bottom: 100%;
    height: 0;
    @include hover(transform, scale(1.05));
  }

  &__link:hover {
    box-shadow: $image-shadow;
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__info {
    text-align: center;
  }

  &__title {
    margin-bottom: 4px;

    font-family: "Cinzel", Arial, Helvetica, sans-serif;
    font-size: 24px;
    font-weight: 700;
    line-height: 140%;
    color: $Accent-main-color;
  }

  &__text {
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-style: normal;
    line-height: 27px;
  }
}
